@import url("https://fonts.googleapis.com/css?family=Nunito:300,400,700&display=swap");
@import url("https://fonts.googleapis.com/css?family=Pacifico&display=swap");
body {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: #f3deca;
  font-weight: 700;
  font-family: "Nunito", sans-serif;
}

.content-container {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

.header {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 4em;
  font-family: "Pacifico", sans-serif;
}

.header__title {
  letter-spacing: 0.15em;
  font-size: 44px;
}

.header__subtitle {
  letter-spacing: 0.05em;
  font-size: 32px;
}

.game-container {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.player-input {
  display: flex;
  justify-content: space-between;
}

.player-input input {
  padding-left: 0.25em;
  width: calc(100% - 26px);
  border-radius: 2px;
  font-size: 18px;
}

.player-input label {
  width: 24px;
  font-size: 22px;
}

.player-input + .player-input {
  margin-top: 0.5em;
}

@keyframes btnToRight {
  0% {
    transform: rotate(0.5turn) translateX(100%);
  }
  100% {
    transform: rotate(0.5turn) translateX(0);
  }
}

@keyframes btnToLeft {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}

.btn {
  position: relative;
  overflow: hidden;
  align-self: center;
  margin-top: 10px;
  padding: 0.25em 0.75em;
  border-radius: 3px;
  background-color: #4097aa;
  color: white;
  letter-spacing: 0.025em;
  font-size: 20px;
  transition: color linear 100ms;
}

.btn::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
}

.btn:hover {
  color: transparent;
}

.btn:hover::after {
  background-image: url("../images/left-arrow.svg");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  transform: rotate(0.5turn);
  animation-name: btnToRight;
  animation-duration: 250ms;
  animation-timing-function: linear;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

.btn_play:hover::after {
  animation-duration: 200ms;
}

.btn_change-players:hover::after {
  animation-name: btnToLeft;
  animation-duration: 300ms;
}

.status {
  font-size: 20px;
}

.board {
  position: relative;
  display: grid;
  overflow: hidden;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  width: 250px;
  height: 250px;
  border-radius: 4px;
  gap: 0.25em;
}

.board span,
.board button {
  background-color: rgba(0, 0, 0, 0.85);
  color: white;
  font-size: 36px;
  cursor: default;
}

.board.is-inactive::before, .board.is-inactive::after {
  position: absolute;
  place-self: center;
  cursor: pointer;
}

.board.is-inactive::before {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  content: "";
  backdrop-filter: blur(3px);
}

.board.is-inactive::after {
  position: absolute;
  color: white;
  content: attr(data-message);
  letter-spacing: 0.05em;
  font-size: 22px;
}

.score-board {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 10px;
  width: 100%;
}

.player-score {
  position: relative;
  display: flex;
  justify-content: space-between;
  letter-spacing: 0.02em;
  font-size: 18px;
}

.player-score::before {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  border-radius: 2px;
  background-color: #4097aa;
  content: "";
  transition: transform linear 150ms;
  transform: scale(0, 1);
}

.player-score.is-active::before {
  transform: scale(1, 1);
}

.player-score:first-child {
  margin-right: 0.5em;
}

.player-score:first-child::before {
  transform-origin: left;
}

.player-score:last-child {
  margin-left: 0.5em;
}

.player-score:last-child::before {
  transform-origin: right;
}
